.mo-cropper {
  text-align: center;
  position: relative;
  font-size: 16px;
  line-height: 16px;
  outline: none;
  /* prevent focus outline */
}
.mo-cropper img {
  user-select: none;
}
.mo-cropper .clipped-image,
.mo-cropper .outer-image {
  width: 100%;
  height: 100%;
  display: block;
}
@-webkit-keyframes cropper-opacity-animation {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.3;
  }
}
@keyframes cropper-opacity-animation {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.3;
  }
}
.mo-cropper.cropper-hidden {
  display: none;
}
.mo-cropper .outer-image-container {
  position: absolute;
  opacity: 0.3;
  left: 0;
  top: 0;
  cursor: move;
}
.mo-cropper.cropper-loading .outer-image-container {
  display: none;
}
.mo-cropper:not(.cropper-hidden) .outer-image-container {
  animation: cropper-opacity-animation 0.2s ease-in;
}
.mo-cropper .clipped {
  position: relative;
  left: 0;
  top: 0;
  max-width: none;
  pointer-events: none;
}
.mo-cropper .cropper-frame {
  box-sizing: border-box;
  position: initial !important;
  /* jquery resizable make this relative and breaks the resizing handle visualization */
  cursor: move;
  z-index: 300;
  left: 0;
  top: 0;
  pointer-events: none;
  /* */
  /* with none the handle is not resizable, without none IE11 doesn't work at all */
}
.mo-cropper .clipping-container {
  overflow: hidden;
  position: relative;
  height: 100%;
  width: 100%;
}
.mo-cropper .clip-handle {
  position: absolute;
  pointer-events: all;
  /* override the cropper-frame pointer-events */
  top: 100%;
  left: 50%;
  width: 0;
  height: 0;
  z-index: 310;
  cursor: row-resize;
  margin-top: -10px;
  transition-property: z-index, margin-top;
  transition-duration: 0.5s, 0.5s;
  /* transition-delay: 200ms, 200ms; */
}
.mo-cropper .clip-handle:before {
  transition-property: background-color, height;
  transition-duration: 0.2s;
  content: "";
  position: relative;
  display: block;
  top: 10px;
  background-color: #CCC;
  box-shadow: 1px 1px 3px #727272;
  left: -25px;
  width: 50px;
  height: 10px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}
.mo-cropper .clip-handle:hover:before {
  height: 12px;
}
.mo-cropper .clip-handle:after {
  content: "\f107";
  /* content: "\f103"; */
  position: relative;
  display: block;
  font-family: FontAwesome;
  color: #eee;
  top: -4px;
  left: -15px;
  width: 30px;
  height: auto;
  border: none;
}
.mo-cropper.cropper-moving.cropper-moving-handle .clip-handle:before {
  background-color: #ff5722;
  height: 15px;
}
.mo-cropper.cropper-moving:not(.cropper-moving-handle) .clip-handle,
.mo-cropper.cropper-loading .clip-handle {
  z-index: -10000;
  margin-top: -30px;
}
.mo-cropper .toolbar {
  position: absolute;
  bottom: 0;
  height: 20px;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  background-image: linear-gradient(transparent, rgba(0, 0, 0, 0.05), rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.6));
  padding: 4px;
  transition-property: opacity, bottom;
  transition-duration: 0.5s;
  transition-timing-function: ease-in-out;
  pointer-events: all;
  z-index: 1;
  opacity: 1;
}
.mo-cropper.mo-cropper.cropper-moving:not(.cropper-moving-slide):not(.cropper-moving-wheel) .toolbar {
  pointer-events: none;
  opacity: 0.1;
  bottom: -40px;
}
.mo-cropper .ui-slider {
  z-index: 1000;
  position: relative;
  height: 4px;
  background-color: white;
  border: none;
  flex: 1 1 50%;
  border-radius: 3px;
  margin: 8px;
  /* default styles from jquery skin, we declare them again as we don't need the jquery ui css */
}
.mo-cropper .ui-slider .ui-slider-handle {
  position: absolute;
  transition-property: background-color, top, width, margin-left, height;
  transition-duration: 0.2s;
  margin-left: -7px;
  top: -5px;
  width: 14px;
  height: 14px;
  background-color: #bfbfbf !important;
  box-shadow: 1px 1px 3px #727272;
  background-image: none;
  border: none;
  border-radius: 100%;
  cursor: ew-resize;
}
.mo-cropper .ui-slider .ui-slider-handle:focus {
  outline: 0 none;
}
.mo-cropper .ui-slider .ui-slider-handle:hover {
  margin-left: -8px;
  top: -6px;
  width: 16px;
  height: 16px;
}
.mo-cropper.mo-cropper.cropper-moving-slide .ui-slider .ui-slider-handle {
  margin-left: -10px;
  top: -8px;
  width: 20px;
  height: 20px;
  background-color: #ff5722 !important;
}
.mo-cropper:not(.cropper-has-changes) .tool-crop .fa:before {
  /* close / fa-times */
  content: "\f00d";
  /* content: "\f125"; */
  /* content: "\f00c"; */
}
.mo-cropper.cropper-loading .tool-crop .fa:before {
  /* loading, important to override the icon change depending on state */
  content: "\f110" !important;
}
.mo-cropper .tool {
  color: white;
  cursor: pointer;
  /* 
        background-color: #ff5722 !important;
        box-shadow: 1px 1px 3px #727272;
        */
  background-image: none;
  border: none;
  border-radius: 100%;
  width: 30px;
  height: 30px;
  z-index: 400;
  padding: 6px;
  box-sizing: border-box;
  flex: 0 0 30px;
  max-width: 30px;
  /* ie10/11 fix */
}
.mo-cropper.cropper-method-cover .tool-zoom .fa:before {
  content: "\f07d";
}
.mo-cropper.cropper-method-resize .tool-zoom .fa:before {
  opacity: 0.5;
}
.mo-cropper.cropper-loading .tool-crop .fa {
  -webkit-animation: fa-spin 2s infinite linear;
  animation: fa-spin 2s infinite linear;
}
